<template>
  <div class="CarControl">
    <div class="reduce" @click="reduceFood()" :style="hiddenStyle">
      <div class="icon-remove_circle_outline"></div>
    </div>
    <div class="count" :style="hiddenStyle">{{foodPayload.count}}</div>
    <div class="add" @click="addFood()">
      <div class="icon-add_circle"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CarControl',
  data () {
    return {
      // foodinfo
      // payload:{
      //   name:,
      //   image:,
      //   price:,
      //   count:
      //   // ...food
      // }
      foodPayload: {
        count: 0
      }
    }
  },
  props: {
    foodinfo: {
      type: Object
    }
  },
  mounted () {
    this.__initShopCar()
  },
  methods: {
    addFood () {
      if (!this.foodPayload.name) { this.__initShopCar() }
      this.$store.commit('addFood', this.foodPayload)
      this.__initShopCar()
    },
    reduceFood () {
      this.$store.commit('reduceFood', this.foodPayload)
      this.__initShopCar()
    },
    // caclTotal () {
    //   return this.$store.getters.caclTotal
    // },
    __initShopCar () {
      // this.caclTotal()
      this.foodPayload = {
        ...this.foodinfo,
        count: this.$store.getters.returnCount(this.foodinfo)
      }
    }
  },
  computed: {
    hiddenStyle () {
      return this.foodPayload.count > 0 ? {} : { visibility: 'hidden' }
    },
    listenshoplist () {
      return this.$store.state.shoplist
    }
  },
  watch: {
    // vuex
    listenshoplist: function () {
      this.__initShopCar()
    }
  }
}
</script>

<style scoped lang="stylus">
.CarControl
  position relative
  right -120px
  bottom 32px
  display inline-block
  .reduce,.count,.add
    display inline-block
  .count
    vertical-align top
    width 12px
    padding-top 6px
    line-height 24px
    text-align center
    font-size 10px
    color rgb(147,153,159)
  .reduce,.add
    padding 6px
    line-height 24px
    font-size 24px
    color rgb(0,160,220)
</style>
